*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary-color: #3D41C1;
  --second-color: #E0F2FE;
}
html{
  font-family: "Roboto";
}
.container{
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.todo_list-container{
  border: 2px solid;
  border-color: #e8e8e8;
  border-radius: 16px;
  overflow: hidden;
  width: 400px;
  height: 500px;
  position: relative;
}
.todo_list-header{
  background: var(--primary-color);
  padding: 16px 16px;
  font-weight: 600;
  text-align: center;
  color: #fff;
}
.todo_list-header .title{
  font-family: "Jost";
  font-size: 16px;
}
.todo_list-header .wrapper{
  display: flex;
  justify-content: space-between;
}
.todo_list-header .time_today{
  text-align: left;
}
.todo_list-header .time_today p:nth-child(1){
  font-size: 18px;
}
.todo_list-header .time_today p:nth-child(2){
  font-size: 12px;
  opacity: 0.8;
}
.add_task-modal .add_button,
.todo_list-header .wrapper .button{
  outline: none;
  border: none;
  padding: 12px 16px;
  border-radius: 8px;
  cursor: pointer;
  color: var(--primary-color);
  background: #fff;
  font-weight: 600;
}
.todo_list{
  list-style: none;
  padding: 12px;
  overflow-y: scroll;
  height: calc(500px - 96px);
}
.todo_list::-webkit-scrollbar{
  display: none;
}

.todo_item{
  width: 100%;
  padding: 16px 40px;
  background: var(--second-color);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  margin: 8px 0;
}
.todo_item span p:nth-child(1){
  font-weight: 600;
}
.todo_item span p:nth-child(2){
  font-size: 14px;
  opacity: 0.8;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.todo_item i{
  font-size: 20px;
}
.task_content,
.add_task-modal .input_wrapper{
  padding: 16px;
}
.add_task-modal .input_wrapper p{
  outline: none;
}
.add_task-modal .add_button{
  background: var(--second-color);
  position: absolute;
  bottom: 16px;
  right: 16px;
}
.task_content p{
  word-wrap: break-word;
}